body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Navbar {
    position: fixed;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    top: 0;
    width: 100vw;
    height: 64px;
    margin: 0;
    padding: 0;
    background-color: rgba(0, 0, 0, 0.8);
    color: #fff;
    -webkit-animation: fadeIn 4s ease-in-out 1;
            animation: fadeIn 4s ease-in-out 1;
    z-index: 9999999;
}

.NavbarHome {
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: 0;
    /* background-color: aqua; */
    height: 100%;
    -webkit-filter: invert(1);
            filter: invert(1);
}

.NavbarHome img {
    height: 36px;
    padding: 14px;
}

.NavbarText {
    -webkit-flex: 2 1;
            flex: 2 1;
    margin: 0;
    /* background-color: green; */
    height: 100%;
    text-align: center;
    line-height: 64px;
    font-size: 24px;
}

.NavbarText::after {
    position: relative;
    top: -8px;
    display: block;
    background: #fff;
    width: 30%;
    height: 2px;
    content: '';
    margin: 0 35% 0 35%;
}

.NavbarLogo {
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: 0;
    height: 64px;
    width: auto;
}

.NavbarLogo img {
    height: 42px;
    padding: 11px;
}
@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.Bottle {
    /* width: 100%; */
    /* margin: 32px; */
    position: absolute;
    width: 100%;
    /* margin: 32px; */
    overflow: hidden;
    height: 80vh;
}

.BottleInfo {
    position: absolute;
    -webkit-animation: fadeIn 8s ease-in-out 1;
            animation: fadeIn 8s ease-in-out 1;
    padding-top: 70vh;
    z-index: 5;
    width: 100%;
}

.BottleInfoText {
    color: white;
    width: 90%;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 8px;
    margin: 5%;
    padding: 4px;
    box-shadow: 1px 1px 8px 0px #00000075;
    /* border: 1px solid #fff; */
}

.Bottle img {
    display: block;
    /* padding: 32px; */
}

.Bottle img.BottleItself {
    position: absolute;
    /* top: 0; */
    /* margin-top: 5vh; */
    width: 90vw;
    /* max-height: 80vh; */
    margin: 5vw;
}

.Bottle img.BottleFg {
    position: absolute;
    top: 0px;
    width: 36vw;
    margin: 32vw;
    margin-top: 33vh;
    margin-left: 40vw;
    z-index: 5px;
}

.BottleTable {
    -webkit-animation: fadeIn 4s ease-in-out 1;
            animation: fadeIn 4s ease-in-out 1;
    margin: 64px 0 0 0;
    width: 100%;
    /* height: 100%; */
    overflow: hidden;
    z-index: -5;
}

.BottleTableBg {
    position: fixed;
    width: 100vw;
    margin: 0;
    height: 50vh;
    bottom: 0;
    background-color: rgb(96, 96, 117);
    z-index: -5;
}

@-webkit-keyframes rotation {
    from {
        -webkit-transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes rotation {
    from {
        -webkit-transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
    }
}

@-webkit-keyframes scale {
    from {
        -webkit-transform: scale(0);
    }
    to {
        -webkit-transform: scale(1);
    }
}

@keyframes scale {
    from {
        -webkit-transform: scale(0);
    }
    to {
        -webkit-transform: scale(1);
    }
}

.box3 {
    width: 100px;
    height: 100px;
    background-color: green;
}
.StartPage,
.MenuPage {
    -webkit-animation: fadeIn 4s ease-in-out 1;
            animation: fadeIn 4s ease-in-out 1;
    margin-top: 16px;
    color: #fff;
}

.StartPage li {
    color: rgb(255, 255, 255);
    list-style: none;
    font-size: 6vw;
    line-height: 10vh;
    font-weight: 100;
    letter-spacing: 1px;
}

.StartPage li:last-child {
    color: rgb(104, 157, 170);
    -webkit-animation: breath 4s ease-in-out infinite;
            animation: breath 4s ease-in-out infinite;
    /* animation: fadeIn 4s ease-in-out 1; */
}

.StartPage img {
    -webkit-animation: breath 4s ease-in-out infinite;
            animation: breath 4s ease-in-out infinite;
    width: 20vw;
    padding: 10vw;
}

.LoadingPage {
    -webkit-animation: breathFade 4s ease-in-out 2;
            animation: breathFade 4s ease-in-out 2;
    animation-direction: reverse;
    padding-top: 30vh;
    width: 100vw;
}

.LoadingPage img {
    width: 20vw;
}

.LoadingPage h2 {
    font-size: 20px;
    line-height: 12vw;
    font-weight: 100;
    color: #fff;
}

.MenuPageTitle {
    padding: 0 20px;
}

.MenuPageTitle img {
    /* flex: 1; */
    float: left;
    -webkit-animation: breath 4s ease-in-out infinite;
            animation: breath 4s ease-in-out infinite;
    height: 12vw;
}

.MenuPageTitle h2 {
    font-size: 20px;
    line-height: 12vw;
    font-weight: 100;
}

.MenuPageItem {
    /* height: 15vh; */
    background: #ffffff10;
    margin: 6vw 10vw;
    border-radius: 20px;
    /* padding: 5vw 0; */
}

.MenuPageItem h1 {
    margin: 0;
}

.MenuPageItem p {
    font-size: 16px;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-bottom: 16px;
    /* overflow: hidden; */
}

.MenuPageItem h1,
p {
    font-weight: 300;
    text-align: left;
    padding: 2px;
    margin: 8px 28px;
}

@-webkit-keyframes rotation {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotation {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@-webkit-keyframes breath {
    0% {
        opacity: 0.2;
    }
    50% {
        opacity: 0.8;
    }
    100% {
        opacity: 0.2;
    }
}

@keyframes breath {
    0% {
        opacity: 0.2;
    }
    50% {
        opacity: 0.8;
    }
    100% {
        opacity: 0.2;
    }
}

@-webkit-keyframes breathFade {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0.6;
    }
    100% {
        opacity: 0;
    }
}

@keyframes breathFade {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0.6;
    }
    100% {
        opacity: 0;
    }
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

html {
    background-color: #000;
}

.App {
    text-align: center;
    /* height: 100vh; */
    width: 100vw;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

